<script>
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
export default {
  data(){
    return{
      currentDate:{},
      mesec:"",
      mesecBroj:0,
      dani:[],
      brojDana:0,
      prviDanUMesecu:0,
      danUNedelji:0,
      trenutniDan:{},
      godina:2022,
      currentPlanner:{},
      validation:"",
      validation2:"",
      dani1:[],
      dani2:[],
      dani3:[],
      dani4:[],
      dani5:[],
      plannerNumber:0,
      mobile:false,

      trenutniDanIndex:0,

      lockedPlanner:false,
      iamthekey:false,


      mesecX:"Decembar",
      godinaX:2022,
      daniX:[],
    }
  },
  computed: mapGetters(["formulari","user","planners"]),
  methods:{
  ...mapActions(['getAllFormulars',"getProfile","submitMesecPlanner","getAllPlanners","putPlanner","lockPlanner"]),

  async ucitaj(){
    await this.getAllPlanners();
    this.currentPlanner=this.planners[this.plannerNumber];
    this.reload();
    const d = new Date();
    let dtime= d.getHours();
    let dmin= d.getMinutes();
    let strMin="";
    if(dmin<10){
      strMin="0"+dmin;
    }else{
      strMin=dmin;
    }
    this.validation2="Ucitan Planer. Vreme ucitavanja: " + dtime +":"+strMin;

  },
  async lockPlannerthis(){
      let data={
        lock:true,
        id:this.currentPlanner._id,
      }

      console.log("pre pucanja");
      await   this.lockPlanner(data);
      console.log("posle pucanja");
      await this.getAllPlanners();
      this.currentPlanner=this.planners[this.plannerNumber];
      this.iamthekey=true;
      this.reload();
  },
  async unlockPlannerthis(){
      let data={
        lock:false,
        id:this.currentPlanner._id,
      }

      await   this.lockPlanner(data);
      await this.getAllPlanners();
      this.currentPlanner=this.planners[this.plannerNumber];
      this.iamthekey=false;
      this.reload();
  },
  addPlanner(){
    this.daniX=[];


    let komentarX = {
      milenko:[],
      natasa:[],
      zorica:[],
      milica:[],
    }

    for (var i = 0; i < 31; i++) {
      let dan={
        mesec:this.mesecX,
        dan:i,
        komentar:komentarX,
        sastanci:[],
      }
      this.daniX.push(dan);
    }

    let data = {
      mesec:this.mesecX,
      godina:this.godinaX,
      dani:this.daniX,
    }

    this.submitMesecPlanner(data);
  },
  selectTrenutni(i){
    this.trenutniDan=this.dani[i];
    this.trenutniDanIndex=i;
  },
  ukloniSastanak(i){
    this.trenutniDan.sastanci.splice(i,1);
  },
  ukloniTask(i,j){
    if(j==1){
      this.trenutniDan.komentar.milenko.splice(i,1);
    }
    if(j==2){
      this.trenutniDan.komentar.natasa.splice(i,1);
    }
    if(j==3){
      this.trenutniDan.komentar.zorica.splice(i,1);
    }
    if(j==4){
      this.trenutniDan.komentar.milica.splice(i,1);
    }

  },
  increaseSastanak(){
    let emptyStr="";

    let sastanak ={
      name: emptyStr,
      startTime:"",
      endTime:"",
    }

    this.trenutniDan.sastanci.push(sastanak);
  },
  increaseTask(i){
    let emptyStr="";
    let chk=false;
    let task = {
      name:emptyStr,
      checked:chk
    };
    if(i==1){
      this.trenutniDan.komentar.milenko.push(task);
    }
    if(i==2){
      this.trenutniDan.komentar.natasa.push(task);
    }
    if(i==3){
      this.trenutniDan.komentar.zorica.push(task);
    }
    if(i==4){
      this.trenutniDan.komentar.milica.push(task);
    }
  },
  async submit(){

    await this.getAllPlanners();
    this.currentPlanner=this.planners[this.plannerNumber];
    if(this.currentPlanner.locked && (this.iamthekey!=true)){
      this.validation="Planner je zakljucan, ne mozete vrsiti izmene."
    }else{

    this.validation="";
    let mesec = this.mesec;
    let godina = this.godina;
    let dani= [];
    for (var i = 0; i < this.dani.length; i++) {
      if(this.dani[i].dan!=0){
        dani.push(this.dani[i])
      }
    }
    // SORTING START
    let testSort =[];
    for ( i = 0; i < dani.length; i++) {
      testSort.push(dani[i]);
    }

    //Svaki dan
    for ( i = 0; i < testSort.length; i++) {
      //i - ti element u sortiranom
      for (var j = 0; j < testSort[i].sastanci.length; j++) {
         let minHour=25;
         let minMinute=61;
         let minIndex=0;
        for (var k = j; k < testSort[i].sastanci.length; k++) {

          let hour = parseInt((testSort[i].sastanci[k].startTime.substring(0, 2)));

          let minute = parseInt((testSort[i].sastanci[k].startTime.substring(3, 5)));

          //if
          if(hour<minHour){

            minHour=hour;
            minMinute=minute;
            minIndex=k;


          }else{
            if(hour==minHour){
              if(minute<minMinute){

                minHour=hour;
                minMinute=minute;
                minIndex=k;


              }
            }
          }
          //if end
        }

        //swap elements
        let temp = testSort[i].sastanci[j];
        // console.log("temp ");
        // console.log(temp);
        testSort[i].sastanci[j]=testSort[i].sastanci[minIndex]
        // console.log("testSort[i].sastanci[j]");
        // console.log(testSort[i].sastanci[j]);
        testSort[i].sastanci[minIndex]=temp
        // console.log("testSort[i].sastanci[minIndex]");
        // console.log(testSort[i].sastanci[minIndex]);
        //swap end


      }


    }

    for ( i = 0; i < testSort.length; i++) {
      // console.log("DAN "+i+".");
      for ( j = 0; j < testSort[i].sastanci.length; j++) {
        // console.log(testSort[i].sastanci[j]);

      }
    }

    // SORTING END

    let data={
      mesec,
      godina,
      dani,
      id:this.currentPlanner._id,
    }
    await   this.putPlanner(data);
    this.validation=`Uspesno sacuvan mesec ${mesec} ${godina}.`;
    await this.getAllPlanners();
    //this.reload();

    }
  },
  reload(){
    this.dani=[];

    if(this.currentPlanner.locked){
      this.lockedPlanner=true;
    }else{
      this.lockedPlanner=false;
    }

    for (var i = 0; i < this.brojDana+this.prviDanUMesecu; i++) {
      let dan;
      if(i<this.prviDanUMesecu){
        dan={
          mesec:"",
          dan:0,
          komentar:{},
          sastanci:"",
        }
      }else{


        dan={
          mesec:this.mesec,
          dan:i+1-this.prviDanUMesecu,
          // komentar:this.currentPlanner.dani[i-this.prviDanUMesecu].komentar,

          komentar:this.currentPlanner.dani[i-this.prviDanUMesecu].komentar,
          sastanci:this.currentPlanner.dani[i-this.prviDanUMesecu].sastanci,
        }





      }



      this.dani.push(dan);
    }
    this.dani1=[];
    this.dani2=[];
    this.dani3=[];
    this.dani4=[];
    this.dani5=[];
    for ( i = 0; i < 7; i++) {
      this.dani1[i]=this.dani[i];
    }
    for ( i = 7; i < 14; i++) {
      this.dani2[i-7]=this.dani[i];
    }
    for ( i = 14; i < 21; i++) {
      this.dani3[i-14]=this.dani[i];
    }
    for ( i = 21; i < 28; i++) {
      this.dani4[i-21]=this.dani[i];
    }
    for ( i = 28; i < this.dani.length; i++) {
      this.dani5[i-28]=this.dani[i];
    }

    this.trenutniDan=this.dani[this.trenutniDanIndex];

  },
  nextMonth(){
    if(this.mesecBroj<11){
    this.mesecBroj+=1;

    this.prviDanUMesecu=(this.prviDanUMesecu+this.brojDana)%7;

    if(this.mesecBroj==0){
      this.mesec="Januar";
      this.brojDana=31;
    }
    if(this.mesecBroj==1){
      this.mesec="Februar";
      this.brojDana=28;
    }
    if(this.mesecBroj==2){
      this.mesec="Mart";
        this.brojDana=31;
    }
    if(this.mesecBroj==3){
      this.mesec="April";
        this.brojDana=30;
    }
    if(this.mesecBroj==4){
      this.mesec="Maj";
        this.brojDana=31;
    }
    if(this.mesecBroj==5){
      this.mesec="Jun";
        this.brojDana=30;
    }
    if(this.mesecBroj==6){
      this.mesec="Jul";
        this.brojDana=31;
    }
    if(this.mesecBroj==7){
      this.mesec="Avgust";
        this.brojDana=31;
    }
    if(this.mesecBroj==8){
      this.mesec="Septembar";
        this.brojDana=30;
    }
    if(this.mesecBroj==9){
      this.mesec="Oktobar";
        this.brojDana=31;
    }
    if(this.mesecBroj==10){
      this.mesec="Novembar";
        this.brojDana=30;
    }
    if(this.mesecBroj==11){
      this.mesec="Decembar";
        this.brojDana=31;
    }
    this.plannerNumber+=1;
    this.currentPlanner=this.planners[this.plannerNumber];

    this.trenutniDanIndex=9;


    // for ( var i = 0; i < this.planners.length; i++) {
    //   if(this.planners[i].mesec == this.mesec){
    //     this.currentPlanner=this.planners[i];
    //   }
    // }

    this.reload();

  }
  },
  lastMonth(){

    if(this.mesecBroj>0){



    this.plannerNumber-=1;
    this.mesecBroj-=1;
    if(this.plannerNumber<3){
      this.plannerNumber=3;
      this.mesecBroj=0;
    }



    if(this.mesecBroj==0){
      this.mesec="Januar";
      this.brojDana=31;
    }
    if(this.mesecBroj==1){
      this.mesec="Februar";
      this.brojDana=28;
    }
    if(this.mesecBroj==2){
      this.mesec="Mart";
        this.brojDana=31;
    }
    if(this.mesecBroj==3){
      this.mesec="April";
        this.brojDana=30;
    }
    if(this.mesecBroj==4){
      this.mesec="Maj";
        this.brojDana=31;
    }
    if(this.mesecBroj==5){
      this.mesec="Jun";
        this.brojDana=30;
    }
    if(this.mesecBroj==6){
      this.mesec="Jul";
        this.brojDana=31;
    }
    if(this.mesecBroj==7){
      this.mesec="Avgust";
        this.brojDana=31;
    }
    if(this.mesecBroj==8){
      this.mesec="Septembar";
        this.brojDana=30;
    }
    if(this.mesecBroj==9){
      this.mesec="Oktobar";
        this.brojDana=31;
    }
    if(this.mesecBroj==10){
      this.mesec="Novembar";
        this.brojDana=30;
    }
    if(this.mesecBroj==11){
      this.mesec="Decembar";
        this.brojDana=31;
    }


    this.currentPlanner=this.planners[this.plannerNumber];

    // for (var  i = 0; i < this.planners.length; i++) {
    //   if(this.planners[i].mesec == this.mesec){
    //     this.currentPlanner=this.planners[i];
    //   }
    // }

    this.prviDanUMesecu=(this.prviDanUMesecu-this.brojDana)%7;
    if(this.prviDanUMesecu<0){
      this.prviDanUMesecu=this.prviDanUMesecu+7;
    }
    this.trenutniDanIndex=9;
    this.reload();


    }}
  },


async created(){

  if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
    this.mobile=true;
  } else {
    this.mobile=false;
  }

  await this.getProfile();
  await this.getAllPlanners();
  this.currentDate = new Date();

  this.currentPlanner=this.planners[12];
  this.plannerNumber=12;

  if(this.currentPlanner.locked){
    this.lockedPlanner=true;
  }else{
    this.lockedPlanner=false;
  }

  this.originalPlanner=this.planners[10];

  if (this.currentDate.getMonth()==0) {
    this.mesec="Januar";
    this.brojDana=31;
  }
  if (this.currentDate.getMonth()==1) {
    this.mesec="Februar";
    this.brojDana=28;
  }
  if (this.currentDate.getMonth()==2) {
    this.mesec="Mart";
    this.brojDana=31;
  }
  if (this.currentDate.getMonth()==3) {
    this.mesec="April";
    this.brojDana=30;
  }
  if (this.currentDate.getMonth()==4) {
    this.mesec="Maj";
    this.brojDana=31;
  }
  if (this.currentDate.getMonth()==5) {
    this.mesec="Jun";
    this.brojDana=30;
  }
  if (this.currentDate.getMonth()==6) {
    this.mesec="Jul";
    this.brojDana=31;
  }

  this.mesecBroj=this.currentDate.getMonth();

  this.danUNedelji=this.currentDate.getDay();
  this.prviDanUMesecu=(this.danUNedelji-this.currentDate.getDate())%7;
  if(this.prviDanUMesecu<0){
    this.prviDanUMesecu=this.prviDanUMesecu+7;
  }


  this.reload();



  this.trenutniDan=this.dani[this.prviDanUMesecu-1+this.currentDate.getDate()];
  this.trenutniDanIndex=this.prviDanUMesecu-1+this.currentDate.getDate();


},


}
</script>

